import React from 'react';
import { connect } from 'react-redux';
import { ButtonBase, Grid, Input } from '@material-ui/core';
import Slider from '@material-ui/lab/Slider';
import uuid from 'uuid/v4';
import PropTypes from 'prop-types';

import BetHistory from '../components/bet-history';
import Container from '../components/base-container';
import ContractObserver from '../libs/contract-observer';


import colors from '../libs/styleguide/colors';
import texts from '../libs/styleguide/texts';
import EosClient from '../libs/eos-client';

import { actions, selectors } from '../redux';

const getPayout = (currentBetNumber) => {
  const expectedValue = 98.46;
  return (expectedValue / (currentBetNumber - 1)).toFixed(2);
};

function EosAmount(props) {
  const { amount = 0.0000, style = {} } = props;

  const styles = {
    root: Object.assign({
      display: 'flex',
      flexDirection: 'row-reverse',
      background: colors.darkGrey2,
      margin: 0,
    }, style)
  };
  return (
    <Container style={styles.root}>
      <span>EOS</span>
      <span>{amount.toFixed(4)}</span>
    </Container>
  );
}

EosAmount.propTypes = {
  amount: PropTypes.number,
  style: PropTypes.object,
};

class Balance extends React.Component {
  state = {
    balance: 0.0000
  }

  componentDidMount() {
    EosClient.getInstance().then(async (eosClient)=>{
      let result = await eosClient.getMyBalance();
      let balance = parseFloat(result[0].replace('EOS', '').trim());
      if (!isNaN(balance)) {
        this.setState({ balance });
      }
    });
  }

  render() {
    const styles = {
      container: {
        display: 'flex',
        alignItems: 'center',
      },
      eosAmount: {
        flexBasis: 'auto',
        flexGrow: 1,
      }
    };
    return (
      <Container style={styles.container}>
        <img src="static/img/icon-game-wallet.svg" />
        <EosAmount style={styles.eosAmount} amount={this.state.balance}/>
      </Container>
    );
  }

}

