class OddsInfo extends React.Component {
  render() {
    const Content = ({ height, text1, text2 }) => {
      const styles = {
        root: {
          height,
          display: 'table',
          width: '100%',
          background: colors.darkGrey2,
          borderRadius: 4,
        },
        wrapper: {
          display: 'table-cell',
          textAlign: 'center',
          verticalAlign: 'middle',
        },
        text1: {
          fontSize: 28,
          fontWeight: 500,
          height: 28,
          color: colors.clearBlue,
        },
        text2: {
          fontSize: 14,
          fontWeight: 500,
          height: 20,
          marginTop: 4,
          color: '#d2d2d2',
        }
      };

      return (
        <div style={styles.root}>
          <div style={styles.wrapper}>
            <div style={styles.text1}>{text1}</div>
            <div style={styles.text2}>{text2}</div>
          </div>
        </div>
      );
    };

    const { currentBetNumber } = this.props;
    const winChance = `${currentBetNumber - 1}%`;
    const payOut = getPayout(currentBetNumber);

    const styles = {
      middleSpacing: { marginTop: 5 }
    };

    return (
      <Container>
        <Content height={120} text1={currentBetNumber} text2={"小於該數獲勝"} />
        <Grid container spacing={8} style={styles.middleSpacing}>
          <Grid item xs={6}>
            <Content height={69} text1={payOut} text2={"賠率"} />
          </Grid>
          <Grid item xs={6}>
            <Content height={69} text1={winChance} text2={"中獎概率"} />
          </Grid>
        </Grid>
      </Container >
    );
  }
}
OddsInfo.propTypes = {
  currentBetNumber: PropTypes.number
};

@connect(({ betAmount, rollUnder, betStatus }) => ({ betAmount, rollUnder, betStatus }))
class Dice extends React.Component {
  state = {
    rollingNumber: 50,
  }

  handleRollUnderChange = (event, value) => {
    this.props.dispatch(actions.updateRollUnder(value));
  }

  handleBetAmountChange = (event) => {
    const { value } = event.target;
    if (isNaN(value) === false && value >= 0) {
      this.props.dispatch(actions.updateBetAmount(event.target.value));
    }
  }

  // eslint-disable-next-line
  handleBetButton = async (event) => {
    const rollUnder = selectors.getRollUnder(this.props);
    const betAmount = selectors.getBetAmount(this.props);

    const betClientId = uuid();
    const quantity = `${parseFloat(betAmount).toFixed(4)} EOS`;
    const memo = `${rollUnder}#peter1#userseed#${betClientId}`;

    const trx = await this.eosClient.transfer(quantity, memo);
    // eslint-disable-next-line
    console.log(`Transaction ID: ${trx.transaction_id}`);

    this.props.dispatch(actions.rollDice(betClientId));
    this.contractObserver.addEvent({
      type: 'onBetResult',
      betClientId,
      fn: (bet) => {
        this.props.dispatch(actions.rollFinished(bet));
        // eslint-disable-next-line
        console.log('[onBetResult]', bet);
      }
    });
  }

  componentDidMount() {
    EosClient.getInstance().then(async (eosClient) => {
      this.eosClient = eosClient;
      let betReceipts = await eosClient.getLatestBetReceipts(100);
      this.props.dispatch(actions.updateBetHistory(betReceipts));
      this.contractObserver = new ContractObserver(eosClient, this.props.dispatch);
      this.contractObserver.start();
    });

    setInterval(() => {
      const betStatus = selectors.getBetStatus(this.props);
      if (betStatus.isRolling) {
        this.setState({ rollingNumber: Math.ceil(Math.random() * 99) });
      }
    }, 100);
  }

  render() {

    const styles = {
      boardTextTitle: {
        fontSize: 20,
        color: '#ffffff',
        textAlign: 'center',
        lineHeight: '56px',
      },
      selectedCoinButton: {
        width: 75,
        height: 42,
        borderRadius: 4,
        backgroundImage: colors.yellow,
      },
      coinButton: {
        width: 75,
        height: 42,
        borderRadius: 4,
        backgroundImage: colors.yellow,
      },
      betButton: {
        height: 48,
        width: '100%',
        borderRadius: 4,
        backgroundImage: colors.yellow,
      },
      icon: {
        verticalAlign: 'middle',
      },
      eosFrame: {
        display: 'flex',
        flexDirection: 'row-reverse',
        alignItems: 'center',
        height: 36,
        borderRadius: 4,
        backgroundColor: colors.darkGrey2,
        marginLeft: 36 + 5,
      },
      adjustButtons: {
        textAlign: 'center',
        height: 25,
        width: '100%',
        borderRadius: 4,
        border: 0,
        color: '#ffffff',
        backgroundColor: colors.darkGrey2,
      }
    };

    const rollUnder = selectors.getRollUnder(this.props);
    const betAmount = selectors.getBetAmount(this.props);
    const betStatus = selectors.getBetStatus(this.props);

    return (
      <div>
        <Balance />
        <CpuAndRam />

        <br />

        <OddsInfo currentBetNumber={rollUnder} />

        <br />
        <Container>
          <div>
            <img src="static/img/icon-game-coin.svg" style={styles.icon} />
            <span>投注金額</span>
          </div>
          <br />

          <div>
            <div style={{ "float": 'left', width: 36 }}>
              <img src="static/img/token-eos.svg" style={styles.icon} />
            </div>
            <div style={styles.eosFrame}>
              <span>EOS</span>
              <Input disableUnderline={true} type="number" inputProps={{
                style: {
                  color: '#ffffff',
                  fontSize: 16,
                  backgroundColor: 'transparent',
                  border: 0,
                }
              }} value={parseFloat(betAmount).toFixed(2)} onChange={this.handleBetAmountChange} />
            </div>
          </div>
          <br />

          <Grid container spacing={8}>
            <Grid item xs={3}><ButtonBase style={styles.adjustButtons}>1/2</ButtonBase></Grid>
            <Grid item xs={3}><ButtonBase style={styles.adjustButtons}>2x </ButtonBase></Grid>
            <Grid item xs={3}><ButtonBase style={styles.adjustButtons}>MIN</ButtonBase></Grid>
            <Grid item xs={3}><ButtonBase style={styles.adjustButtons}>MAX</ButtonBase></Grid>
          </Grid>

          <br />
          <Slider min={2} max={96} step={1}
            value={rollUnder}
            onChange={this.handleRollUnderChange}
          />
          <br />

          <div>
            <img src="static/img/icon-game-coin.svg" style={styles.icon} />
            <span>贏得獎金</span>
          </div>
          <br />

          <div>
            <div style={{ "float": 'left', width: 36 }}>
              <img src="static/img/token-eos.svg" style={styles.icon} />
            </div>
            <div style={styles.eosFrame}>
              <EosAmount amount={betAmount * getPayout(rollUnder)}/>
            </div>
          </div>
        </Container>

        <br />
        <div style={{ margin: '0px 10px 0px 10px' }}>
          <ButtonBase style={styles.betButton} onClick={this.handleBetButton}>
            <div style={texts.ButtonLarge}>{betStatus.isRolling ? this.state.rollingNumber : '開始投注'}</div>
          </ButtonBase>
        </div>

        <br />

        <BetHistory />

        <style jsx global>{`
          body {
            width: 100%;
            height: 1000px;
            background-color: ${colors.darkGrey2};
            font-family: Roboto, Helvetica, Arial, sans-serif;
            margin: 0px;
            overflow-x: hidden;
            color: #ffffff;
          }
        `}</style>

      </div>
    );
  }
}
Dice.propTypes = {
  dispatch: PropTypes.func,
};

export default Dice;